            }

            .Return {
                background-color: white;
                display: flex;
                flex-direction: column;
                align-items: center;
                padding-top: 4px;
                padding-bottom: 4px;

                .Code {
                    color: var(--black);
                    font-family: Amiko, sans-serif;
                    font-size: 16px;
                    font-style: normal;
